document.addEventListener("DOMContentLoaded", function () {
  scrollNav();

  navegacionFija();
});

function scrollNav() {
  //Seleccionando todos los enlaces de la navegación principal
  const enlaces = document.querySelectorAll(".navegacion-principal a");

  enlaces.forEach(function (enlace) {
    enlace.addEventListener("click", function (e) {
      //Para quitar la acción por defecto
      e.preventDefault();
      //Para seleccionar el id a donde va #
      const seccion = document.querySelector(e.target.attributes.href.value);

      //Para que el efecto de scroll sea más lento
      seccion.scrollIntoView({
        behavior: "smooth",
      });
    });
  });
}

function navegacionFija() {
  //Seleccionar la barra
  const barra = document.querySelector(".header");

  //Registrar el intersection observer
  const observer = new IntersectionObserver(function (entries) {
    //isvisible no es confiable, por lo que hay que utilizar isIntersecting
    if (entries[0].isIntersecting) {
      barra.classList.remove("fijo");
    } else {
      barra.classList.add("fijo");
    }
  });

  //Elemento a observar
  observer.observe(document.querySelector(".sobre-festival"));
}
